.cf:after {content: ''; display: block; clear: both;}
.detailWrap { background: #000; color: #fff; padding: 6%;
    .leftBox { float: left; width: 50%; text-align: center;
        img {width: 263px; border-radius: 7px; display: block; margin: 0 auto;}
        a {width: 263px; padding: 12px 0; display: inline-block; color: #fff; background: #36be36; border-radius: 3px; margin-top: 10px; font-weight: bold; font-size: 1.1rem;}
    }

    .rightBox { float: left; width: 50%; padding-right: 50px;
        h3 {font-size: 2.2rem; padding-bottom: 20px;}
        p {font-size: 1.1rem;}
        p:last-child{color: rgba(255,255,255,.6); font-size: 1rem; padding-top: 30px;}
    }
}