.detailLnk {
    text-decoration: none; color: #fff;
    .movies {
        // 1. float 방식
        // float:left; width:25%;
    
        // 2. flex 방식
        // flex: 1;
    
        margin: 20px 10%;
        img {width: 100%; border: 3px solid #fff;}
        .movies_data {
            h3 {font-size: 16px; color: rgba(255,255,255,1);}
            h5 {color: rgba(255,255,255,.8);}
            ul {list-style: none; display: flex;
                flex-wrap: wrap;
                li {padding-right: 20px;}
            }
            p {color: rgba(255,255,255,.3); font-size: 0.8rem;}
        }
    }
}

