
.bg{ 
    max-width: 1170px; 
    margin: 0 auto;
    background: #000;
    color: #fff;
    .aboutWrap { text-align: center; border: 5px solid rgb(42, 190, 62); padding: 20px 50px 30px 50px; margin: 0 5% 5%;
        h3 {font-size: 2rem; border-bottom: 1px solid rgba(255,255,255,.3); width: 80%; margin: 0 auto; padding-bottom: 10px;}
        div { padding: 10px 0; margin-bottom: 26px;
            p { padding: 20px 0; color: #abcc00; font-weight: bold;
                strong {background: #AD0000; padding: 0 5px; color: #fff;}
            }
            h5 {color: #4c96d0; font-size: 1.2rem; font-weight: bold;}
        }
        a {padding: 10px 30px; color: #fff; background: #239823; border-radius: 4px; text-decoration: none;}
    }
}
