.loadWrap {
    font-size: 50px;
    min-height: 600px;
    text-align: center;
    line-height: 600px;
    .loadicon {
        font-size: 5rem; color: #fff;
        animation: loadspin infinite 3s linear;
    }
}

@keyframes loadspin {
    0% {}
    100% {transform: rotate(360deg);}
}